// @ts-nocheck
import "./App.css";
import React, { useEffect, useState } from "react";

//ui
import { Button } from "@material-ui/core";

//logic
import { DAppClient } from "@airgap/beacon-sdk";
//import _ from "underscore";
//import { FirebaseContext } from "./firebaseContext";
import { Profile } from './components/profile';
import { useNavigate } from "react-router-dom";


const dAppClient = new DAppClient({ name: "Beacon Docs" });


function App() {
  const navigate = useNavigate();
  const [activeAccount, setActiveAccount] = useState();
  const [synced, setSynced] = useState("sync");
  const [showUnsync, setShowUnsync] = useState(false);
  const [address, setAddress] = useState();

  useEffect(() => {
    async function getAddress() {
      console.log(new URL(window.location.href).searchParams.get('profile'))
      let profileAddress = new URL(window.location.href).searchParams.get('profile');
      //setAddress(profileAddress);

      if (profileAddress && !(profileAddress.includes(".tez"))) {
        setAddress(profileAddress);
      }
      else {

        await fetch('https://api.tezos.domains/graphql', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            query: `
                {
                  domain(name: "` + profileAddress + `") {
                    name
                    address
                  }
                }
                `,
            variables: {
            },
          }
          ),
        })
          .then((res) => res.json())
          .then((result) => {
            console.log(result);
            if (result.data.domain) {
              setAddress(result.data.domain.address);
            }
          });
      }


    }
    getAddress();
  }, []);

  useEffect(() => {
    async function getAcc() {
      setActiveAccount(await dAppClient.getActiveAccount());
      if (activeAccount) {
        setSynced(
          activeAccount.address.slice(0, 6) +
          "..." +
          activeAccount.address.slice(32, 36)
        );
        setShowUnsync(true);
        if (!address) {
          navigate("/Assemble?profile=" + activeAccount.address);
        }
      } else {
        setSynced("sync");
        setShowUnsync(false);
      }
    }
    getAcc();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [activeAccount]);

  async function unsync() {
    setActiveAccount(await dAppClient.getActiveAccount());
    if (activeAccount) {
      // User already has account connected, everything is ready
      dAppClient.clearActiveAccount().then(async () => {
        setActiveAccount(await dAppClient.getActiveAccount());
        setSynced("sync");
        setShowUnsync(false);
      });
    }
  }

  async function sync() {
    setActiveAccount(await dAppClient.getActiveAccount());
    //Already connected
    if (activeAccount) {
      setSynced(activeAccount.address);
      setShowUnsync(true);
      if (!address) {
        navigate("/Assemble?profile=" + activeAccount.address);
      }
      return activeAccount;
    }
    // The user is not synced yet
    else {
      try {
        console.log("Requesting permissions...");
        const permissions = await dAppClient.requestPermissions();
        setActiveAccount(await dAppClient.getActiveAccount());
        console.log("Got permissions:", permissions.address);
        setSynced(permissions.address);
        setShowUnsync(true);
      } catch (error) {
        console.log("Got error:", error);
      }
    }
  }

  return (
    <div>
      <div
        className="top-left"
        style={{
          fontSize: "1em",
          display: "flex",
          alignItems: "center",
          margin: 6,
        }}
      >
        <b>Assemble </b>
      </div>

      {!showUnsync &&
        <div>

          <div style={{ textAlign: "center", margin: 100 }}>
            welcome sync and explore
          </div>
          <div style={{ textAlign: "center", margin: 100 }}>
            profile search module
          </div>
          <div style={{ textAlign: "center", margin: 100 }}>
            display random profile
          </div>
        </div>
      }
      {
        (address || activeAccount) &&
        <Profile
          address={address ? address : activeAccount.address}
          account={activeAccount}
        />
      }

      <div
        className="top-right"
        style={{ position: "absolute", display: "flex", alignItems: "center" }}
      >
        {showUnsync && (
          <Button
            size={"small"}
            title={"unsync"}
            onClick={() => {
              unsync();
            }}
          >
            <u>unsync</u>{" "}
          </Button>
        )}

        {showUnsync && <div> | </div>}
        <Button
          title={"sync"}
          size={"small"}
          onClick={async () => {
            await sync();
          }}
        >
          <u>{synced}</u>{" "}
        </Button>
      </div>
    </div>
  );
}

export default App;
